import { addResult } from '../utils/commonUtils';

//    //TODO
export function flowerFaan(flowers, seat, resultDict) {
    let faanCount = 0
    const groups = [];
    for (const flower of flowers) {
        faanCount += 1;
        groups.push(flower);
        if (flower % 10 === seat) {
            faanCount += 1;
        }
    }
    if (faanCount > 0) {
        addResult(resultDict, "", groups, faanCount);
    } else {
        addResult(resultDict, "", [], faanCount);
    }
}

// 
export function noFlowerNoHonor(resultDict) {
    if ("" in resultDict && "" in resultDict) {
        delete resultDict[""];
        delete resultDict[""];
        addResult(resultDict, "");
    }
}

export function noFlowerNoHonorAllSequence(resultDict) {
    if ("" in resultDict && "" in resultDict) {
        delete resultDict[""];
        delete resultDict[""];
        addResult(resultDict, "");
    }
}