// 
import { tiles } from "./constant"
import { addResult } from './commonUtils'


//
export function noHonorTile(fullHandCount,  resultDict) {
    let honorTiles = tiles["faanzi"]; // 

    honorTiles.forEach(tile => {
        if (fullHandCount[tile] > 0) {
            return false;
        }
    });

    addResult(resultDict, "");
    return true;
}


//    
export function honorTile(fullHandCount, wind, seat, resultDict) {
    let honorTiles;
    if ("" in resultDict || "" in resultDict || "" in resultDict || "" in resultDict) {
        honorTiles = tiles["dragons"]; //avoid double count 
    } else if ("" in resultDict || "" in resultDict) {
        honorTiles = tiles["winds"]; //avoid double count 
    } else {
        honorTiles = tiles["faanzi"]; // 
    }
    let faanCount = 0;

    honorTiles.forEach(tile => {
        if (fullHandCount[tile] >= 3) {
            faanCount += 1;
            if (tile > 10) {  // 2
                faanCount += 1;
            }
            if (tile === wind) {
                faanCount += 1;
            }
            if (tile === seat) {
                faanCount += 1;
            }
        }
    });

    if (faanCount > 0) {
        addResult(resultDict, "", [], faanCount);
    }
}

//, , , 
export function fourHappiness(fullHandCount, resultDict) {
    const windTiles = [1, 3, 5, 7]; //
    let tripletCount = 0;
    let pairCount = 0;
    windTiles.forEach(tile => {
        if (fullHandCount[tile] >= 3) {
            tripletCount++;
        } else if (fullHandCount[tile] === 2) {
            pairCount++;
        }
    });

    if (tripletCount === 4) {
        addResult(resultDict, "");
    } else if (tripletCount === 3 && pairCount === 1) {
        addResult(resultDict, "");
    } else if (tripletCount === 3) {
        addResult(resultDict, "");
    } else if (tripletCount === 2 && pairCount === 1) {
        addResult(resultDict, "");
    }

    return tripletCount >= 3 || (tripletCount >= 2 && pairCount === 1);
}


// 	
export function threeChiefs(fullHandCount, resultDict) {
    const dragonTiles = tiles["dragons"]; //, , 
    
    let tripletCount = 0;
    let pairCount = 0;
    dragonTiles.forEach(tile => {
        if (fullHandCount[tile] >= 3) {
            tripletCount++;
        } else if (fullHandCount[tile] === 2) {
            pairCount++;
        }
    });

    if (tripletCount === 3) {
        addResult(resultDict, "");
    } else if (tripletCount === 2 && pairCount === 1) {
        addResult(resultDict, "");
    }

    return tripletCount === 3 || (tripletCount === 2 && pairCount === 1);
}